
.component{
 display: flex;
 flex-direction: row;
 background-color:lightblue;
 justify-content: space-evenly;
 padding: 20px 20px 20px 20px ;
  margin:20px 20px 20px 20px  ;
}
.compo{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  box-sizing: border-box;
  color:white;
}
.Cart{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color:white;
}
.compodiv,.cartdiv{
  box-shadow: 10px 10px 10px 10px grey;
  padding: 20px 20px 20px 20px ;
  margin:20px 20px 20px 20px  ;
  border-radius: 4px;
  border-color: yellow;
}
.img-compo{
  width: 440px;
  height: 240px;
}
.btn{
  background-color: blue;
  color: white;
  font-size: large;
  font-weight: 200;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  width: 240px;
  height: 50px;
  border-radius: 10px;
}
.header{
  background-color:black;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items: center;
   padding:10px 10px 10px 10px ;
   margin: 0px 10px 10px 10px;
}

h1{
  color: white;
}
.img-login{
  width:240px;
  height: 200px;
}
.img-about,.img-logo{
  width: 400px;
  height: 200px;
}
.App{
  background-color: lightblue;
  text-align: center;
}
.login{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color:white;
  box-sizing: content-box;
}
.btn-login{
  background-color: gold;
  border-radius: 10px;
  width: 450px;
  height: 40px;
  padding: 10px 10px 10px 10px ;
  margin: 10px 10px 10px 10px ;
  font-size: medium;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}
input{
  width:400px;
  height: 30px;
  padding: 10px 10px 10px 10px ;
  margin: 10px 10px 10px 10px ;
}
h1{
  color:rgb(67, 31, 182)
}
p{
  font-size: x-large;
  font-family: fantasy;
  color:black;
}
h2{
  color: blue;
  font-size: x-large;
}
.info{
  padding: 10px 10px 10px 10px ;
  margin: 10px 10px 10px 10px ;
}